import { Component, OnInit } from '@angular/core';
import { HttpEventType } from '@angular/common/http';
import { FileService } from '../services/file.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.page.html',
  styleUrls: ['./upload.page.scss'],
})
export class UploadPage implements OnInit {

  registerForm:FormGroup;
  selectedFiles = null;
  progress = null;
  model: any = {};

  constructor(private fb:FormBuilder,private router:Router,private fileservice:FileService) {
  }

  ngOnInit() {

  }

  onFileSelected(event) {
    console.log(event.target.files[0]);
     this.selectedFiles=event.target.files[0]
  }

  onUpload() {
    if (this.selectedFiles != null) {
      const formData = new FormData();
      formData.append('file',this.selectedFiles);  
      formData.append('title',this.model.title);
      formData.append('code',this.model.code);  
      formData.append('subject',this.model.subject);
      formData.append('year',this.model.year);
      formData.append('notetype',this.model.notetype);
       this.fileservice.upload(formData).subscribe(event => {
        console.log(event);
      });
      this.router.navigateByUrl('/download');
    }
  }


}
